.login{position:relative;min-height:100vh;background:linear-gradient(-45deg, #362d2b, #e9756e, #f5ddba, #eabeae);background-size:400% 400%;-webkit-animation:gradientBG 10s linear infinite;animation:gradientBG 10s linear infinite;font-weight:bold}.login-form{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);padding:15px 30px;border:5px solid #e9756e;border-radius:5px;width:250px;background-color:rgba(255,255,255,0.8)}.login-form .login_title{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.login-form .login_title img{width:30px;margin-right:5px}.login-form .login_title span{margin-right:10px}.login-form .login_title i{cursor:pointer}.login-form .form-item{display:-webkit-flex;display:flex}.login-form .form-item label{display:block;width:30px}.login-form .form-item input{width:100%}.login-form div{margin-bottom:20px}.login-form p{text-align:center;font-size:14px}.login-form p span{margin:0 5px;cursor:pointer}.login-pic{position:absolute;background-repeat:no-repeat;background-size:cover;background-position:center center;height:250px;background-color:#fff;border:5px solid #e9756e;border-radius:5px;opacity:0.7}.login-pic-left{background-image:url(/static/media/handmade-bg03.891b9630.jpg);right:60px;top:5%;width:30%}.login-pic-right{background-image:url(/static/media/handmade-bg02.e60b2fc0.jpg);left:60px;bottom:5%;width:30%}.login-pic-left-top{background-image:url(/static/media/handmade-bg01.1535d15e.jpg);width:250px;height:250px;left:15%;top:10%}.login-pic-right-bottom{background-image:url(/static/media/handmade-bg04.9b06c29a.jpg);width:300px;height:300px;right:10%;bottom:10%}@media (max-width: 768px){.login-pic{display:none}}

